<template>
  <section>
    <div v-on:click="removeFromCart(item.id)" class="close"><i class="fi fi-close-a"></i> <span class="rmv"> remove from cart</span></div>
  </section>
</template>

<script>
export default {
  name: "remove",
  methods: {
      removeFromCart(id) {
          this.$emit("remove-cart", id)
      }
  },
  props: {
      item: Object
  },
  emits: ["remove-cart"]
};
</script>

<style scoped>
.close {
    width: 150px;
    height: 20px;
    padding: 2px;
    margin-top: 50px;
    font-size: 14px;
    color: red;
    background: #f2f2f266;
    box-shadow: 0 0 3px gray;
    border-radius: 2px;
    cursor: pointer;
    font-family: 'Montserrat', sans-serif;  
}

@media screen and (max-width: 400px) {
  .rmv {
    display: none;
  }
  .close {
    width: 20px;
    height: 20px;
    padding: 3px;
    margin-top: 30px;
  }
}
</style>